"use client";

import { useState } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { CreditCard } from "lucide-react";

interface PaymentCodeDialogProps {
  orderId: string;
  onSubmit: (paymentCode: string) => void;
  children: React.ReactNode;
}

export function PaymentCodeDialog({ orderId, onSubmit, children }: PaymentCodeDialogProps) {
  const [paymentCode, setPaymentCode] = useState("");
  const [isOpen, setIsOpen] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (paymentCode.trim()) {
      onSubmit(paymentCode.trim());
      setPaymentCode("");
      setIsOpen(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        {children}
      </DialogTrigger>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <CreditCard className="h-5 w-5" />
            Submit Payment Code
          </DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="payment-code">Payment Code</Label>
            <Input
              id="payment-code"
              placeholder="Enter your payment code"
              value={paymentCode}
              onChange={(e) => setPaymentCode(e.target.value)}
              required
            />
            <p className="text-sm text-muted-foreground">
              Enter the payment code for order {orderId}
            </p>
          </div>
          <div className="flex gap-2">
            <Button type="submit" className="flex-1">
              Submit Code
            </Button>
            <Button type="button" variant="outline" onClick={() => setIsOpen(false)}>
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}